.wedding-party__container {

    border-radius: 8px;
    text-align: center;
    padding-top: 60px;

    .wedding-party {
        display: flex;
        padding: 15px 20px;
        grid-gap: 20px;
        width: 100%;
        margin: 0 auto; 
        justify-content: space-around;

        @media (max-width: 800px) {
            flex-direction: column;

            .vl {
                border-left: none;
                border-top: 1px solid rgba(218, 165, 32, 0.637);
                width: 60%;
                height: 1px;
                margin: auto;
            }
        }
    }

    .flower-girl {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding-bottom: 75px;
    }

    .wedding-party__grid-item {
        font-size: 30px;
        width: fit-content;
        width: 400px;

        .wedding-party__title {
            font-style: italic;
            color: goldenrod;
            
        }

         @media (max-width: 800px) {
             width: 100%;
         }
    }



    .vl {
        border-left: 1px solid rgba(218, 165, 32, 0.637);
        height: 200px;
        width: fit-content;
        margin: auto 0;
    }

}